import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	data() {
		return {
			order: [],
			num: 1,
			height: '100%'
		}
	},
	computed: {
		...mapState(['cartList','total'])
	},
	onLoad() {
		this.order = this.cartList;
		var h = uni.getSystemInfoSync().windowHeight - 100;
	},
	methods: {
		...mapMutations(['removeCart','calcTotal']),
		goMenu(){
			uni.switchTab({
				url: '/pages/menu/index'
			})
		},
		minus(i) {
			if(this.order[i].num >1){
				this.order[i].num -- ;
			}else{
				this.order[i].num = 1;
				var _this = this;
				uni.showModal({
					content: '确认不要了吗？',
					success: function (res) {
						if (res.confirm) {
							_this.removeCart(i)
						}
					}
				});
			}
			this.calcTotal(this.order)
		},
		plus(i) {
			this.order[i].num ++;
			this.calcTotal(this.order);
		}
	}
}
